import React from 'react';
import styled from 'styled-components';
import { Banner } from './homeStyledComponents';
import { colors, mobileThresholdPixels } from '../styledComponents';
import photo from '../../assets/Josephine.jpg';

const Container = styled.div`
  position: relative;
`;

const InvertedBlock = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 20px 0px;
  background-color: ${colors.lightGrey};

  @media (max-width: ${mobileThresholdPixels}) {
    flex-direction: column;
    padding: 2px 0px;
  }
`;

const Column = styled.div`
  margin: 20px;
  max-width: 45%;
  display: flex;
  flex-direction: column;

  @media (max-width: ${mobileThresholdPixels}) {
    margin: 2px;
    max-width: 100%;
  }
`;

const P = styled.p`
  color: ${colors.navy};
`;

const Img = styled.img`
  object-fit: cover;
  height: 400px;
`;

const BannerContainer = styled.div`
  position: absolute;
  bottom: 40px;
`;

const HowItWorks = () => (
  <Container>
    <InvertedBlock>
      <Column>
        <P>{"Votre couturier se dplace jusqu' chez vous, dans Paris et la Petite Couronne."}</P>
        <P>  </P>
        <P>{'Votre couturier est  votre disposition tous les jours de 17.00  21.30.'}</P>
        <P>  </P>
        <P>{'Votre couturier est un artisan professionnel, choisi avec soin par tilli.'}</P>
      </Column>
      <Column>
        <Img src={photo} alt="" />
      </Column>
    </InvertedBlock>
    <BannerContainer><Banner /></BannerContainer>
  </Container>
);

export default HowItWorks;



