import React, { PropTypes } from 'react';
import styled from 'styled-components';
import { colors } from '../styledComponents';
import { Icon } from './homeStyledComponents';

const Container = styled.div`
  margin: 10px;
  flex: 1;
  text-align: center;
`;

const Number = styled.span`
  padding: 10px 25px;
  color: ${colors.navy};
  margin: 20px;
  text-align: center;
  font-size: 40px;
  font-weight: 300;
  border-radius: 80px;
  border: 1px solid #333;

  &:hover {
    border-width: 2px;
    font-weight: bold;
  }
`;

const Body = styled.div`
  margin-top: 20px;
`;

const NumerotedBlock = ({ number, children, icon }) => (
  <Container>
    {icon ?
      <Icon src={icon} alt="" />
      : <Number>{number}</Number>
    }
    <Body>{children}</Body>
  </Container>
);

NumerotedBlock.propTypes = {
  icon: PropTypes.string,
  number: PropTypes.number,
  children: PropTypes.node.isRequired,
};

NumerotedBlock.defaultProps = {
  icon: '',
  number: undefined,
};

export default NumerotedBlock;



